/* forms */
.input-text_wrapper label,
.input-text_wrapper input{
    display: block;
    width: 100%;
    font-size: 0.8rem;
}
.input-text_wrapper input {
    border: 1px solid #000;
    border-radius: 3px;
    position: relative;
    z-index: 1;
    background: transparent;
    margin-bottom: 10px;
    padding: 10px;
    width: 100%;
}
.button_wrapper button{
    width: 100%;
    padding: 10px;
    background: #000;
    color: #fff;
    border: none;
    border-radius: 5px;
}
.button_wrapper button:disabled{
    opacity: 0.3;
}
.button_wrapper h2{
    font-size: 1rem;
    font-weight: 700;
}
.form-title{
    margin-bottom: 35px;
}
.input-text_wrapper{
    position: relative;
}
.input-label{
    background-color: blue; 
    z-index: 0;
}
.input-label.active{
    position: relative;
    background-color: red;
}
.select-wrapper select{
    width: 100%;
    background: transparent;
    border: 1px solid #000;
    border-radius: 5px;
}
.danger{
    text-align: center;
    color: red;
    font-weight: 700;
    margin-top: 30px;
}
.m-bottom-30{
    margin-bottom: 10px;
}
#signup form{
    width: 90%;
}
/* end */